.login-form-container {
    max-width: 400px;
    padding: 30px;
    background: linear-gradient(90deg, #a8e6cf, #dcedc1, #c2e9fb);
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin-top: 150px;
    color: #333;
    margin-left: 450px;
}

h1 {
    margin-top: 0px;
    margin-bottom: 20px;
    color: #333;
}

.form-group {
    margin-bottom: 20px;
    text-align: left;
    margin-right: 20px;
}

.form-group input {
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    background-color: #ffffff;
}

.form-group input::placeholder {
    color: #777;
}

.form-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.form-options label {
    font-size: 14px;
    color: #555;
}

.forgot-password {
    font-size: 14px;
    color: #007bff;
    text-decoration: none;
    margin-left: 175px;
}

.forgot-password:hover {
    text-decoration: underline;
}

.error-message {
    color: #e57373;
    font-size: 14px;
    margin-bottom: 15px;
    text-align: left;
}

.login-button {
    width: 100%;
    padding: 12px;
    background-color: #6fbad3;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-button:disabled {
    background-color: #a3a9b5;
    cursor: not-allowed;
}

.login-button:hover:not(:disabled) {
    background-color: #579ab3;
}